*{
    box-sizing: border-box;
}
body {
    margin: 0;
    padding: 0%;
    font-family:cursive;
    background-color: rgba(228, 170, 116, 0.911);
}

html,
body, 
#butter,
.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    width: 80%;
}

.movies .movie {
    width: 45%;
    background-color: antiquewhite;
    margin-bottom: 50px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 200;
    padding: 15px;
    border-radius: 5px;

    position: relative;
}

.movie img {
    max-height: 220px;
    margin-right: 30px;
    top: -7px;

    position: relative;
}

.movie .movie_title {
    font-weight: 250;
    margin: 0;
}

.movie .movie_year {
    opacity: 0.5;
}

.movie .movie_rate {
    color:brown;
}

.movie .movie_genres {
    color:rgb(59, 49, 6);
    padding: 0;
    margin: 0;
}

.movie .movie_year {
    padding: 0;
    margin: 0;
    font-size: 17px;
}

.movie .movie_summary {
    margin: 0;
}